.wca-customizer-control--typeface .wca-customizer-control__header .components-dropdown {
  display: block;
}
.wca-customizer-control--typeface .wca-customizer-control__header .components-dropdown .components-popover__content {
  min-width: 230px;
  overflow: hidden;
}
.wca-customizer-control--typeface .wca-customizer-control__header .components-dropdown .components-popover__content > div {
  padding: 0;
}
.wca-customizer-control--typeface .wca-customizer-control__header button {
  width: 100%;
  border: 1px solid lightgray;
  box-shadow: none;
  background-color: transparent;
}
.wca-customizer-control--typeface .wca-customizer-control__header button .ff-name {
  width: 65%;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1;
  text-align: left;
}
.wca-customizer-control--typeface .wca-customizer-control__header button .ff-preview {
  line-height: 1;
  margin-left: auto;
  font-size: 21px;
}
.wca-customizer-control--typeface .wca-customizer-control__header .select-inline {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}
.wca-customizer-control--typeface .wca-customizer-control__header .select-inline > .customize-control-title {
  margin: 0;
  max-width: 35%;
}
.wca-customizer-control--typeface .wca-customizer-control__header .select-inline > * {
  flex-grow: 1;
}
.wca-customizer-control--typeface .wca-customizer-control__header .select-inline select {
  border-radius: 0;
  border-color: lightgray;
}
.wca-customizer-control--typeface .wca-customizer-control__header .select-inline .components-base-control__field {
  margin: 0;
}
.wca-customizer-control--typeface .wca-customizer-control__font-weights .components-select-control__input {
  height: auto !important;
  min-height: 150px !important;
}
.wca-customizer-control--typeface .wca-customizer-control__font-weights .components-input-control__suffix {
  display: none !important;
}
.wca-customizer-control--typeface .wecodeart-fonts {
  position: relative;
}
.wca-customizer-control--typeface .wecodeart-fonts__search {
  position: absolute;
  top: -40px;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid #ccc;
  padding: 5px;
  box-sizing: border-box;
}
.wca-customizer-control--typeface .wecodeart-fonts__search .components-base-control {
  width: 85%;
}
.wca-customizer-control--typeface .wecodeart-fonts__search .components-base-control__field {
  margin-bottom: 0;
}
.wca-customizer-control--typeface .wecodeart-fonts__search .components-base-control__field input {
  height: 25px;
  width: 100%;
}
.wca-customizer-control--typeface .wecodeart-fonts__search .close-font-selector {
  padding: 0 5px;
  margin-left: auto;
}
.wca-customizer-control--typeface .wecodeart-fonts__list {
  margin-top: 40px;
  overflow-y: auto;
  max-height: 300px;
}
.wca-customizer-control--typeface .wecodeart-fonts__list .load-more {
  padding: 10px 0;
}
.wca-customizer-control--typeface .wecodeart-fonts__list .load-more svg {
  fill: #0073aa;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li {
  margin: 0;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li.selected a {
  background-color: #0b69da;
  color: #fff;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li.no-result {
  opacity: 0.75;
  font-weight: 600;
  padding: 10px 0;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li.font-group-header {
  text-align: left;
  padding: 5px 10px;
  font-size: 15px;
  font-weight: 600;
  background-color: #efefef;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li a {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  transition: 0.2s ease;
  color: #333;
  border-bottom: 1px solid #efefef;
  cursor: pointer;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li a:hover {
  color: #fff;
  background-color: #0073aa;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li a .wca-font-family {
  font-size: 14px;
  font-weight: 400;
  text-align: left;
  max-width: 75%;
  overflow: hidden;
  text-overflow: ellipsis;
}
.wca-customizer-control--typeface .wecodeart-fonts__list li a .wca-font-preview {
  line-height: initial;
  font-display: swap;
  margin-left: auto;
  font-size: 20px;
}